z przez zespół teatralny „Rairewa”, którego jest twórcą i reżyserem. Występuje z recitalami poezji śpiewanej, wykonując przy akompaniamencie gitary i harmonijki ustnej swoje kompozycje. Pisze teksty i muzykę dla innych wykonawców. W 2019 roku został nakręcony film dokumentalny o jego twórczości i życiu pt.: "Wędrowiec". W 2022 roku na mikołowskim rynku odsłonięto dwie rzeźby jego autorstwa : Rafała Wojaczka i Kozią rodzinę. Jest „wędrowcem idącym w nieznane , by nie odczuwać żalu za poznanym” – jak sam zwykł powtarzać motto swej drogi twórczej i życiowej.'
