<template>
  <v-app>
    <v-app-bar v-if="selectedKind === 'painting'" app class="white" elevation="0">
      <v-slider v-model="sliderIndex" @change="changeYear()" class="mx-16 transparent"
        :tick-labels="years" :max="years.length-1" step="1" ticks="always"
        tick-size="4" thumb-color="orange" track-fill-color="orange" track-color="grey"></v-slider>
    </v-app-bar>


    <v-navigation-drawer app permanent>
      <v-row class="ma-16 align-center justify-center">
        <v-avatar size="96px">
          <v-img alt="Maciej Paździor photo" :src="require('@/assets/imgs/photo.webp')" />
        </v-avatar>
      </v-row>

      <v-row class="align-center justify-center">
        <v-list nav dense>
          <v-list-item v-for="item in menu" :key="item.title" @click="changeSection(item.kind, item.elements, item.year)" class="text-center">
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-row>

      <template v-slot:append>
        <div class="my-16">
          <v-divider class="ma-6"></v-divider>
          <v-row align="center" justify="center" style="font-size: 0.8em; font-weight: 900;">© {{new Date().getFullYear()}} Maciej Paździor</v-row>
          <v-row align="center" justify="center" style="font-size: 0.7em;">Wykonanie: 
          <v-btn class="ma-0 pa-1" style="font-size: 0.8em;" href="https://niec.me/" target="_blank" plain>Adrian Nieć</v-btn></v-row>
        </div>
      </template>
    </v-navigation-drawer>

    <v-main>
      <Gallery v-if="selectedElements > 0" class="ma-1" :kind="selectedKind" :elements="selectedElements" :year="selectedYear" />
      <About v-if="selectedKind === 'about'" />
    </v-main>
  </v-app>
</template>

<script>
import Gallery from './components/Gallery';
import About from './components/About';

export default {
  name: 'App',
  components: {
    Gallery,
    About,
  },
  metaInfo() {
    return {
      title: "Maciej Paździor Art",
      meta: [
        { name: 'description', content: 'This website is prezentation of Maciej Paździor art.', },
        { property: 'og:title', content: "Maciej Paździor Art" },
        { property: 'og:site_name', content: 'Maciej Paździor Art' },
        { property: 'og:description', content: 'This website is prezentation of Maciej Paździor art.' },
        { property: 'og:type', content: 'website' },
        { name: 'robots', content: 'index,follow' }
      ]
    }
  },
  data: () => ({
    isBarVisible: false,
    selectedKind: "painting",
    selectedElements: 9,
    selectedYear: 2023,
    sliderIndex: null,
    years: ['2023', '2022', '2021', '2020', '2017-2019', '2014-2016', '2011-2013', '2005-2010', '2000-2004', '1992-1999'],
    photosInYear: [9, 40, 25, 16, 42, 35, 37, 42, 37, 48],
    menu: [
    {
      title: "Malarstwo",
      kind: "painting",
      elements: 9,
    },
    {
      title: "Rysunek",
      kind: "drawning",
      elements: 109,
    },
    {
      title: "Portret",
      kind: "portrait",
      elements: 31,
    },
    {
      title: "Grafika",
      kind: "graphic",
      elements: 139,
    },
    {
      title: "Rzeźba",
      kind: "sculpture/other",
      elements: 84,
    },
    {
      title: "O mnie",
      kind: "about",
      elements: 0,
    },
  ],
  }),
  methods: {
    changeSection(name, elements) {
      this.selectedKind = name
      this.selectedElements = elements
      this.selectedYear = name === 'painting' ? 2023 : null
      this.sliderIndex = 0
      scroll(0,0)
    },
    changeYear(){
      this.selectedYear = this.years[this.sliderIndex]
      this.selectedElements = this.photosInYear[this.sliderIndex]
      this.$forceUpdate();
    }
  },
};
</script>
