<template>
  <v-row>
<v-col
      v-for="n in elements"
      :key="n"
      class="d-flex child-flex"
      cols="2"
    >
      <v-img
      max-height="250px"
      max-width="250px"
        :src="getPhotoPath(n, flase)"
        :lazy-src="getPhotoPath(n, true)"
        aspect-ratio="1"
        class="grey lighten-2"
      >
        <template v-slot:placeholder>
          <v-row
            class="fill-height ma-0"
            align="center"
            justify="center"
          >
            <v-progress-circular
              indeterminate
              color="grey lighten-5"
            ></v-progress-circular>
          </v-row>
        </template>
      </v-img>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "Gallery",
  props: ["kind", "elements", "year"],
  data: () => ({
  }),
  methods: {
    getPhotoPath(element, isBlured=true) {
    return require('@/assets/imgs/' + (isBlured? 'blur' : 'mini') + '/' + 
        this.kind +
        "/" +
        (this.year ? this.year + "/" : "") +
        String(element).padStart(this.elements.toString().length, "0") +
        ".webp");
  },
  },
  computed: {
  getPhotoPath() {
    return (element) =>
      require('@/assets/imgs/mini/' + 
        this.kind +
        "/" +
        (this.year ? this.year + "/" : "") +
        String(element).padStart(this.elements.toString().length, "0") +
        ".webp");
  },
},
}
</script>